.list-group-item {
    border-radius: 0 !important;
    background-color: rgba(0, 0, 0, 0);
    -webkit-transition: ease-in-out 0.1s;
    transition: ease-in-out 0.1s;
    border: none !important;
}

.list-group-item:hover {
    background-color: #fc8259 !important;
    -webkit-transition: ease-in-out 0.1s;
    transition: ease-in-out 0.1s;
}

.active {
    background-color: #ee5c2b !important;
}

.btn-orange {
    background-color: #ee5c2b !important;
    color: white;
}

.btn-orange:hover {
    background-color: #fc8259 !important;
    color: white;
}

.text-orange {
    color: #ee5c2b !important;
}

